html,
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: rgb(218, 219, 223);
 
}
.wrapper {
  max-width: 500px;
  min-height: 100vh;
  /*margin: 0 auto;*/
  margin-top: 30px;
  margin-left: 30px;
  
  
  
}
.inicio{
    margin-top:-640px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
button {
  font-size: 2rem;
  border: 2px solid #333;
  cursor: pointer;
  border-radius: 15%;
}
.modifiers button {
  background-color: #ff963e;
}
.operations button {
  background-color: orange;
  width: 100px;
}
.digits button {
  background-color: #ffd5b1;
}

/* All things Grid, Flexbox, Box Alignment */
.wrapper,
.subgrid {
  display: grid;
}
.wrapper {
  grid-template-areas:
    "total total total total"
    "modif modif modif oper"
    "digit digit digit oper"
    "digit digit digit oper"
    "digit digit digit oper"
    "digit digit digit oper";
  grid-auto-columns: 1fr;
}
.show-input {
  grid-area: total;
  background: #fff;
  border:2px solid #333 ;
  max-width: 470px;
  border-radius: 4%;
  margin-top: 20px;
}
.modifiers {
  grid-area: modif;
}
.operations {
  grid-area: oper;
}
.digits {
  grid-area: digit;
}

.show-input {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 2rem;
}

.modifiers {
  grid-auto-flow: column;
  grid-auto-columns: 1fr;
}

.digits {
  display: flex;
  flex-wrap: wrap;
}
.digits button {
  flex: 1 0 26%;
}
